import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Avatar,
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  Link,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  Typography,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import getInitials from 'src/utils/getInitials';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  },
  {
    value: 'hasAcceptedMarketing',
    label: 'Đã có LMS'
  },
  {
    value: 'isProspect',
    label: 'Chưa có LMS'
  },
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên trường (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên trường (tăng)'
  }
];

const applyFilters = (schools, query, filters) => {
  return schools.filter((school) => {
    let matches = true;

    if (query) {
      const properties = ['email', 'name'];
      let containsQuery = false;

      properties.forEach((property) => {
        if (school[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && school[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (schools, page, limit) => {
  return schools.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (schools, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = schools.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  className,
  schools,
  ...rest
}) => {
  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedSchools, setSelectedSchools] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedSchools([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllSchools = (event) => {
    setSelectedSchools(event.target.checked
      ? schools.map((school) => school.id)
      : []);
  };

  const handleSelectOneSchool = (event, schoolId) => {
    if (!selectedSchools.includes(schoolId)) {
      setSelectedSchools((prevSelected) => [...prevSelected, schoolId]);
    } else {
      setSelectedSchools((prevSelected) => prevSelected.filter((id) => id !== schoolId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredSchools = applyFilters(schools, query, filters);
  const sortedSchools = applySort(filteredSchools, sort);
  const paginatedSchools = applyPagination(sortedSchools, page, limit);
  const enableBulkOperations = selectedSchools.length > 0;
  const selectedSomeSchools = selectedSchools.length > 0 && selectedSchools.length < schools.length;
  const selectedAllSchools = selectedSchools.length === schools.length;

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllSchools}
              indeterminate={selectedSomeSchools}
              onChange={handleSelectAllSchools}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
            >
              Xóa
            </Button>
            <Button
              variant="outlined"
              className={classes.bulkAction}
            >
              Sửa
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={1300}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllSchools}
                    indeterminate={selectedSomeSchools}
                    onChange={handleSelectAllSchools}
                  />
                </TableCell>
                <TableCell>
                  Tên
                </TableCell>
                <TableCell>
                  Domain
                </TableCell>
                <TableCell>
                  Địa chỉ
                </TableCell >
                <TableCell>
                  Quận/Huyện
                </TableCell>
                <TableCell>
                  Email
                </TableCell>
                <TableCell>
                  Số  điện thoại
                </TableCell>
                <TableCell>
                  LMS URL
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedSchools.map((school) => {
                const isSchoolSelected = selectedSchools.includes(school.id);

                return (
                  <TableRow
                    hover
                    key={school.id}
                    selected={isSchoolSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isSchoolSelected}
                        onChange={(event) => handleSelectOneSchool(event, school.id)}
                        value={isSchoolSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                        <Avatar
                          className={classes.avatar}
                          src={school.icon}
                        >
                          {getInitials(school.name)}
                        </Avatar>
                        <div>
                          <Link
                            color="inherit"
                            component={RouterLink}
                            to={"/school/management/classes"}
                            variant="h6"
                            onClick={ ()=>{
                              localStorage.setItem('schoolid', school.id);
                              localStorage.setItem('schoolname', school.name);
                            }}
                          >
                            {school.name}
                          </Link>
                          <Typography
                            variant="body2"
                            color="textSecondary"
                          >
                            {school.email}
                          </Typography>
                        </div>
                      </Box>
                    </TableCell>
                  <TableCell>
                  {school.id}
                  </TableCell>
                  <TableCell style={{width: '20%'}}>
                  {school.address}
                  </TableCell>
                    <TableCell>
                      {`${school.province}, ${school.district}`}
                    </TableCell>
                    <TableCell>
                    {school.email}
                    </TableCell>
                    <TableCell>
                      {school.phone}
                    </TableCell>
                    <TableCell>
                      {school.lms_url}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton
                        component={RouterLink}
                        to={"/hbon/management/school/"+ school.id+"/edit"}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/classes"}
                        
                        onClick={ ()=>{
                          localStorage.setItem('schoolid', school.id);
                          localStorage.setItem('schoolname', school.name);
                        }}
                      >
                        <SvgIcon fontSize="small">
                          <ArrowRightIcon />
                        </SvgIcon>
                      </IconButton>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredSchools.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  schools: PropTypes.array.isRequired
};

Results.defaultProps = {
  schools: []
};

export default Results;
