import React, {
  useState,
  useEffect,
  useCallback
} from 'react';
import {
  Box,
  Container,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import Header from './Header';
import Results from './Results';
import {listAllSchool} from '../../../api/schools'

import  { Redirect } from 'react-router-dom'
import useAuth from 'src/hooks/useAuth';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  }
}));

const SchoolListView = () => {
  const { user} = useAuth();
  const renderRedirect = () => {
    if(user && user.roles && user.roles.some(el => el === "hbonadmin")){
        console.log("admin");
      }
      else {
        console.log("khong co quyen");
        return <Redirect to='/404' />
      }
  }
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [schools, setSchools] = useState([]);

  const getSchools = useCallback(async () => {
    try {
      const schools = await listAllSchool();
      console.log(schools)
      if (isMountedRef.current) {
        setSchools(schools);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getSchools();
  }, [getSchools]);

  return (
    <Page
      className={classes.root}
      title="Trường học"
    >
      {renderRedirect()}
      <Container maxWidth={false}>
        <Header />
        <Box mt={3}>
          <Results schools={schools} />
        </Box>
      </Container>
    </Page>
  );
};

export default SchoolListView;
